body {
  background-color: darkslategray;
  font-weight: bolder;
  text-transform: lowercase;

  color: white;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

h1 {
  font-size: 3em;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
#success {
  color: lime;
}
#mid {
  color: orange;
}
#fail {
  color: red;
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.headers {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
}
.tutorials {
  position: absolute;
  top:0;
  left:0;
width: 100vw;
height: 100vh;
color: whitesmoke;
background-color: darkslategray;
display: flex;
justify-content: center;
text-align: center;
align-items: center;
line-height: 200%;
}
.headers #tut {
  margin-bottom: -1vw;
  text-decoration: none;
  color: lightcoral;
  font-weight: bold;
  cursor: grab;

}
#timeStatus {
  color: lightcoral;
  animation: RedtoGreen forwards 20s linear;
}
@keyframes RedtoGreen {
from {color: greenyellow};
to {color: red};
}

.header {
  display: flex;
  justify-content: center;
  text-align: center;
  color: white;
}

.header-inner {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: -2vw;
}
.choice {
  cursor: grab;
  width: 80px;
  height: 80px;
  background-color: white;
  margin: 10px;
  color: black;
  border-radius: 2px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.choice h6 {
  font-size: 2em;
  
}
.title {
  text-decoration: line-through wavy rgb(255,155,60,0.5);
  font-size: x-large;
}
.choice:hover {
  background-color: rgb(179, 255, 191);
}
h6 {
  margin: none;
  padding: none;
}
.center {
  display: flex;
  justify-content: center;
  text-align: center;
  line-height: 260%;
}

.center-inner {
  max-width: 75%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  white-space: pre-wrap;
}

.hud {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  align-items: center;
}

.hud-inner {
  width: 40vw;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  margin: 10px;
}

.choice-inner {
  width: 200px;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  margin: 10px;
}
.scoreboard {

  display: flex;
  flex-direction: row;
  justify-content: center;
  text-align: center;
  align-items: center;
  padding: 10px;
}

.scoreboard-inner {
  width: 80%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.players {
  color: white;
  display: flex;
  flex-direction: row;
  justify-content: center;
  text-align: center;
  align-items: center;
}

.nextButton {
  cursor: grab;
  width: 150px;
  border: solid 3px #FFFFFF; 
  border-radius: 10px;
}

.nextButton:hover {
  border-color: lime;
  color: lime;
  animation: ohya ease-in-out 0.5s;
}

@keyframes ohya {
  from {border: solid 3px #FFFFFF; color: white;};
  to {border: solid 3px lime; color: lime;};
}
